.ar-title {
    text-align: center;
    font-size: 1.8rem;
    font-weight: 600;
    color: #333;
    margin: 0;
}

.ar-top-controls {
    display: flex;
    justify-content: end;
    margin-bottom: 1rem;
}

.ar-top-controls button {
    background-color: #493628;
    color: white;
    padding: 0.6rem 1.2rem;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.ar-top-controls button:hover {
    background-color: #5e3c21;
}

.ar-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 1.5rem;
    padding: 1rem 0;
}

.ar-card {
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
    padding: 1.5rem 1rem;
    text-align: center;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.ar-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.ar-card h3 {
    font-size: 1.2rem;
    font-weight: 600;
    color: #333;
    margin: 0.5rem 0 0.25rem;
}

.ar-card p {
    font-size: 0.9rem;
    color: #777;
    margin-bottom: 0;
}

.ar-icon {
    margin-bottom: 10px;
    color: #AB886D;
}

.upload-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
}

.upload-form {
    background: #fff;
    padding: 2rem;
    border-radius: 12px;
    width: 100%;
    max-width: 500px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
    animation: fadeInScale 0.3s ease-in-out;
}

@keyframes fadeInScale {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.upload-form h2 {
    margin-bottom: 1rem;
    font-size: 1.5rem;
    text-align: center;
    color: #333;
}

.upload-form label {
    display: block;
    margin-bottom: 1rem;
    font-weight: 500;
    color: #333;
}

.upload-form input,
.upload-form textarea {
    width: 100%;
    padding: 0.6rem;
    margin-top: 0.3rem;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 0.95rem;
    box-sizing: border-box;
}

.arform-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5rem;
    gap: 1rem;
}

.arform-actions button {
    flex: 1;
    padding: 0.6rem 1.2rem;
    border: none;
    border-radius: 6px;
    font-size: 0.95rem;
    cursor: pointer;
    font-weight: 500;
    transition: background-color 0.2s ease;
}

.arform-actions button[type="submit"] {
    background-color: #22c55e;
    color: white;
}

.arform-actions button[type="button"] {
    background-color: #dc3545;
    color: white;
}

.arform-actions button:hover {
    opacity: 0.95;
}